*{
  font-family: Arial, Helvetica, sans-serif;
}


body {
  padding: 60px;
}

input{
  color: grey;
  width: 100%;
  font-size: 1.3rem;
}


.card{
  align-items: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  text-align: center;
  max-height: 170px;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}